import React, { Fragment } from 'react'
import PropTypes from 'prop-types'

import classNames from 'classnames'
import styles from './FixedScreen.module.scss'

const FixedScreen = ({ indicatorRefs }) => {
  const navigate = targetRef => {
    window.scrollTo({
      top: targetRef.current.offsetTop,
      left: 0,
      behavior: 'smooth'
    })
  }

  return (
    <div className={styles.fixedScreen}>
      <div className={styles.pageIndicator}>
        {
          indicatorRefs && indicatorRefs.map((ref, index) => {
            return (
              <span key={index} className={styles.indicatorContainer}>
                <span className={styles.pageIndicatorLabel}>{ref.label}</span>
                <span ref={ref.source} onClick={() => navigate(ref.target)} className={classNames([styles.pageIndicatorList, ref.isActive ? styles.active : ''])} />
              </span>
            )
          })
        }
      </div>
    </div>
  )
}

FixedScreen.propTypes = {
  indicatorRefs: PropTypes.array.isRequired
};

export default FixedScreen;